.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 50%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out; }

@media (min-width: 500px) {
    .SideDrawer {
        display: none; } }

.Open {
    transform: translateX(0);
    text-align: center; }


.Close {
    transform: translateX(-100%); }

.Logo {
    height: 11%;
    margin-bottom: 32px; }


.closed {
    position: absolute;
    font-size: 45px;
    color: red;
    right: 16px;
    top: 2px;
    cursor: pointer; }

